<template>
  <div class="browse-wildlife-areas">
    <div id="map"></div>
    <figcaption>
      Zoom and/or pan the map to update the list of areas below.
    </figcaption>
    <main class="wla-list">
      <accordion
        v-for="(area, areaIndex) in wildlifeAreas"
        :key="areaIndex"
        :label="`${area.wildlifeAreaName} Wildlife Area`"
        class="wla"
        :hidden="!areaUnitsInExtent(area.units)"
        @hoverOverHeader="hoverOverArea(area)"
        @hoverOutHeader="clearMapGraphics()"
      >
        <template
          v-if="area.units.length > 1"
          slot="accordion-custom-content"
        >
          <template
            v-for="(unit, unitIndex) in area.units"
          >
            <accordion
              :key="unitIndex"
              :label="`${unit.unitName} Unit`"
              class="wlau"
              :hidden="unitFacilityIdsInExtent.indexOf(unit.facilityId) === -1"
              @hoverOverHeader="hoverOverUnit(unit)"
              @hoverOutHeader="clearMapGraphics()"
            >
              <unit-overview
                slot="accordion-custom-content"
                :unit="unit"
                :language-type="'Unit'"
              />
            </accordion>
          </template>
        </template>
        <template
          v-else
          slot="accordion-custom-content"
        >
          <unit-overview
             slot="accordion-custom-content"
             :unit="area.units[0]"
             :language-type="'Wildlife Area'"
          />
        </template>
      </accordion>
    </main>
  </div>
</template>

<script>
import loadEsriModules from '@/helpers/loadEsriModules';
import CreateMap from '@/lib/commonMap';
import Accordion from '@/components/Accordion.vue';
import UnitOverview from '@/components/UnitOverview.vue';

export default {
  name: 'browse-wildlife-areas',
  components: {
    Accordion,
    UnitOverview,
  },
  data() {
    return {
      esriModules: {},
      map: null,
      mapView: null,
      unitLayer: null,
      highlightLayer: null,
      layerView: null,
      featuresInExtent: [],
    };
  },
  computed: {
    unitFacilityIdsInExtent() {
      return this.featuresInExtent.map(feature => feature.attributes.SUBUNIT_ID);
    },
    wildlifeAreas() {
      return this.$store.getters.wildlifeAreas;
    },
  },
  async mounted() {
    this.esriModules = await loadEsriModules();
    const {
      map,
      mapView,
      unitLayer,
    } = CreateMap(this.esriModules);
    this.map = map;
    this.mapView = mapView;
    this.unitLayer = unitLayer;
    this.highlightLayer = new this.esriModules.GraphicsLayer();
    this.map.add(this.highlightLayer);
    this.mapView.whenLayerView(this.unitLayer)
      .then((layerView) => {
        this.layerView = layerView;
        this.layerView.when(
          () => {
            this.esriModules.WatchUtils.whenTrue(
              this.mapView,
              'stationary',
              this.queryFeatureLayerAtExtent,
            );
          },
        );
        this.esriModules.WatchUtils.once(
          this.layerView,
          'updating',
          this.queryFeatureLayerAtExtent,
        );
      });
  },
  beforeDestroy() {
  },
  destroyed() {
    this.map = null;
    this.mapView = null;
    this.unitLayer = null;
    this.highlightLayer = null;
    this.layerView = null;
  },
  methods: {
    queryFeatureLayerAtExtent() {
      if (this.unitLayer.loaded) {
        const query = new this.esriModules.Query();
        query.geometry = this.mapView.extent;
        query.returnGeometry = true;
        this.layerView.queryFeatures(query)
          .then((results) => {
            this.featuresInExtent = results.features;
          });
      }
    },
    areaUnitsInExtent(units) {
      return units.some(unit => this.unitFacilityIdsInExtent.indexOf(unit.facilityId) >= 0);
    },
    async getUnitsById(arrayOfIds) {
      if (this.unitLayer.loaded) {
        const query = new this.esriModules.Query();
        query.where = `SUBUNIT_ID IN (${arrayOfIds.join(',')})`;
        query.returnGeometry = true;
        return this.layerView.queryFeatures(query)
          .then(results => results.features);
      }
      return [];
    },
    async hoverOverArea(area) {
      const features = await this.getUnitsById(area.units.map(unit => unit.facilityId));
      this.addHighlightToMap(features);
    },
    async hoverOverUnit(unit) {
      const features = await this.getUnitsById([unit.facilityId]);
      this.addHighlightToMap(features);
    },
    clearMapGraphics() {
      this.highlightLayer.removeAll();
    },
    addHighlightToMap(features) {
      this.clearMapGraphics();
      const polygons = features.map((feature) => {
        const clone = feature.clone();
        clone.symbol = {
          type: 'simple-fill',
          color: [190, 110, 70, 1],
          outline: {
            color: [0, 0, 0, 0],
          },
        };
        return clone;
      });
      const pointGeometry = this.esriModules.GeometryEngine.union(
        polygons.map(polygon => polygon.geometry),
      );
      const pointGraphic = new this.esriModules.Graphic({ geometry: pointGeometry });
      pointGraphic.symbol = {
        type: 'simple-marker',
        path: `
          M 16 3.5
          c -4.142 0 -7.5 3.358 -7.5 7.5
          c 0 4.143 7.5 18.121 7.5 18.121
          S 23.5 15.143 23.5 11
          C 23.5 6.858 20.143 3.5 16 3.5
          z
          M 16 14.584
          c -1.979 0 -3.584 -1.604 -3.584 -3.584
          S 14.021 7.416 16 7.416
          S 19.584 9.021 19.584 11
          S 17.979 14.584 16 14.584
          z
        `,
        color: '#1A5A91',
        outline: {
          color: '#13426A',
        },
        yoffset: 11,
        size: 22,
      };
      this.highlightLayer.addMany(polygons);
      this.highlightLayer.add(pointGraphic);
    },
  },
};
</script>

<style lang="scss">
@import '@/assets/app_specific.scss';
@import url('https://js.arcgis.com/4.9/esri/css/main.css');

#map {
  width: 100%;
  height: 400px;
  margin-bottom: 15px;
  background-color: $light-gray;
}
</style>
