import { loadModules } from 'esri-loader';

export default async function loadEsriModules() {
  return loadModules([
    'esri/Basemap',
    'esri/geometry/geometryEngine',
    'esri/layers/FeatureLayer',
    'esri/Graphic',
    'esri/layers/GraphicsLayer',
    'esri/layers/VectorTileLayer',
    'esri/Map',
    'esri/views/MapView',
    'esri/tasks/support/Query',
    'esri/core/watchUtils',
  ]).then(([
    Basemap,
    GeometryEngine,
    FeatureLayer,
    Graphic,
    GraphicsLayer,
    VectorTileLayer,
    Map,
    MapView,
    Query,
    WatchUtils,
  ]) => ({
    Basemap,
    GeometryEngine,
    FeatureLayer,
    Graphic,
    GraphicsLayer,
    VectorTileLayer,
    Map,
    MapView,
    Query,
    WatchUtils,
  }));
}
