export function wildlifeAreas(state) {
  const areas = state.wildlifeAreaUnits.reduce((accumulator, unit) => {
    const key = unit.wildlifeAreaName;
    let index = accumulator.indexOf(accumulator.filter(area => area.wildlifeAreaName === key)[0]);
    if (index < 0) {
      accumulator.push({
        wildlifeAreaName: key,
        units: [],
      });
      index = accumulator.length - 1;
    }
    accumulator[index].units.push(unit);
    return accumulator;
  }, []);
  for (let i = 0; i < areas.length; i += 1) {
    areas[i].units = areas[i].units.sort((a, b) => {
      if (a.wildlifeAreaName < b.wildlifeAreaName) return -1;
      if (a.wildlifeAreaName > b.wildlifeAreaName) return 1;
      return 0;
    });
  }
  return areas.sort((a, b) => {
    if (a.wildlifeAreaName < b.wildlifeAreaName) return -1;
    if (a.wildlifeAreaName > b.wildlifeAreaName) return 1;
    return 0;
  });
}

export const wildlifeAreaByURLName = state => urlName => state.wildlifeAreaUnits.find(
  wildlifeArea => wildlifeArea.urlSafeName === urlName,
);

export default {
  wildlifeAreas,
  wildlifeAreaByURLName,
};
