import Vue from 'vue';
import Vuex from 'vuex';
import actions from './actions';
import getters from './getters';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    loaded: false,
    fetchingWildlifeAreaUnits: false,
    fetchingWildlifeAreaUnitDetails: false,
    wildlifeAreaUnits: [],
    wildlifeAreaUnitDetails: {},
  },
  mutations: {
    setLoaded(state, status) {
      state.loaded = status;
    },
    setFetchingStatus(state, status) {
      state[status.key] = status.value;
    },
    setWildlifeAreaUnits(state, units) {
      state.wildlifeAreaUnits = units;
    },
    updateUnitDetails(state, unitDetails) {
      Vue.set(state.wildlifeAreaUnitDetails, unitDetails.facilityId, unitDetails);
    },
  },
  actions,
  getters,
});
