import { fetchWildlifeAreaUnits, fetchWildlifeAreaUnitDetails } from '@/lib/apiRequests';

export async function getWildlifeAreaUnits({ state, commit }) {
  if (state.fetchingWildlifeAreaUnits) return;
  commit('setFetchingStatus', {
    key: 'fetchingWildlifeAreaUnits',
    value: true,
  });
  const units = await fetchWildlifeAreaUnits();
  commit(
    'setWildlifeAreaUnits',
    units,
  );
  commit('setFetchingStatus', {
    key: 'fetchingWildlifeAreaUnits',
    value: false,
  });
  commit('setLoaded', true);
}

export async function getWildlifeAreaUnitDetails({
  state, commit, getters,
}, areaName) {
  if (state.fetchingWildlifeAreaUnitDetails) return;
  const matchingUnit = getters.wildlifeAreaByURLName(areaName);
  if (matchingUnit) {
    commit('setFetchingStatus', {
      key: 'fetchingWildlifeAreaUnitDetails',
      value: true,
    });
    const unitDetails = await fetchWildlifeAreaUnitDetails(matchingUnit.facilityId);
    commit('updateUnitDetails', unitDetails);
    commit('setFetchingStatus', {
      key: 'fetchingWildlifeAreaUnitDetails',
      value: false,
    });
  }
}

export default {
  getWildlifeAreaUnits,
  getWildlifeAreaUnitDetails,
};
