        >
      </figure>
      <!-- Title -->
      <h2>{{ wildlifeAreaUnit.unitName }}</h2>
      <div class="wla-highlights">
        <header>At a Glance</header>
        <span><strong>Acres:</strong> {{ details.totalAcres }}</span>
        <span>
          <strong>Elevation:</strong> {{ details.minElevation }}' - {{details.maxElevation}}'
        </span>
        <span><strong>GMUs:</strong> {{ details.gmus.join(', ')}}</span>
      </div>
      <p v-html="details.generalDescription"></p>
      <p
        v-if="details.locationDescription"
        v-html="details.locationDescription"
      ></p>
      <div id="map"></div>
      <accordion
        :label="'Recreation and Public Access'"
      >
        <template
          slot="accordion-custom-content"
        >
          <template
            v-if="details.publicFacilities.length > 0"
          >
            <header><strong>Public Facility Information:</strong></header>
            <ul>
              <li
                v-for="(publicFacility, index) in details.publicFacilities"
                :key="index"
                v-html="publicFacility"
              ></li>
            </ul>
          </template>
          <accordion
            v-if="showHunting"
            :label="'Hunting'"
          >
            <template
              slot="accordion-custom-content"
            >
              <p
                v-if="details.huntingDescription"
                v-html="details.huntingDescription"
              ></p>
              <p
                v-if="details.specialHuntingOpportunities"
                v-html="details.specialHuntingOpportunities"
              ></p>
              <template
                v-if="details.huntingRestrictions.length > 0"
              >
                <header><strong>Specific Hunting Restrictions:</strong></header>
                <ul>
                  <li
                    v-for="(huntingRestriction, index) in details.huntingRestrictions"
                    :key="index"
                    v-html="huntingRestriction"
                  ></li>
                </ul>
              </template>
            </template>
          </accordion>
          <accordion
            v-if="showFishing"
            :label="'Fishing and Boating'"
          >
            <template
              slot="accordion-custom-content"
            >
              <p
                v-if="details.fishingDescription"
                v-html="details.fishingDescription"
              ></p>
              <p
                v-if="details.specialFishingOpportunities"
                v-html="details.specialFishingOpportunities"
              ></p>
              <template
                v-if="details.fishingRestrictions.length > 0"
              >
                <header><strong>Specific Fishing Restrictions:</strong></header>
                <ul>
                  <li
                    v-for="(fishingRestriction, index) in details.fishingRestrictions"
                    :key="index"
                    v-html="fishingRestriction"
                  ></li>
                </ul>
              </template>
            </template>
          </accordion>
          <accordion
            v-if="showWildlifeViewing"
            :label="'Wildlife Viewing'"
          >
            <template
              slot="accordion-custom-content"
            >
              <p
                v-if="details.wildlifeViewingDescription"
                v-html="details.wildlifeViewingDescription"
              ></p>
              <p
                v-if="details.specialWildlifeViewingOpportunities"
                v-html="details.specialWildlifeViewingOpportunities"
              ></p>
              <template
                v-if="details.wildlifeViewingRestrictions.length > 0"
              >
                <header><strong>Specific Wildlife Viewing Restrictions:</strong></header>
                <ul>
                  <li
                    v-for="(wildlifeViewingRestriction, index)
                      in details.wildlifeViewingRestrictions"
                    :key="index"
                    v-html="wildlifeViewingRestriction"
                  ></li>
                </ul>
