    <template
      v-else-if="$store.state.fetchingWildlifeAreaUnits === false"
    >
      4-O4 Ranch Not Found
    </template>
  </div>
</template>

<script>
import loadEsriModules from '@/helpers/loadEsriModules';
import CreateMap from '@/lib/commonMap';
import Accordion from '@/components/Accordion.vue';

export default {
  name: 'wildlife-area-unit',
  components: {
    Accordion,
  },
  data() {
    return {
      baseURL: process.env.BASE_URL,
      esriModules: {},
      // map: null,
      // mapView: null,
      // unitLayer: null,
    };
  },
  computed: {
    wildlifeAreaUnitURLName() {
      return this.$route.params.wildlifeAreaUnitName;
    },
    wildlifeAreaUnit() {
      return this.$store.getters.wildlifeAreaByURLName(this.wildlifeAreaUnitURLName);
    },
    details() {
      if (this.wildlifeAreaUnit) {
        return this.$store.state.wildlifeAreaUnitDetails[this.wildlifeAreaUnit.facilityId];
      }
      return null;
    },
    showHunting() {
      if (
        this.details.huntingDescription
        || this.details.specialHuntingOpportunities
        || this.details.huntingRestrictions.length > 0
      ) return true;
      return false;
    },
    showFishing() {
      if (
        this.details.fishingDescription
        || this.details.specialFishingOpportunities
        || this.details.fishingRestrictions.length > 0
      ) return true;
      return false;
    },
    showWildlifeViewing() {
      if (
        this.details.wildlifeViewingDescription
        || this.details.specialWildlifeViewingOpportunities
        || this.details.wildlifeViewingRestrictions.length > 0
      ) return true;
      return false;
    },
    showOtherRecreation() {
      if (
        this.details.otherRecreationDescription
        || this.details.specialOtherRecreationOpportunities
        || this.details.otherRecreationRestrictions.length > 0
      ) return true;
      return false;
    },
    showConservationGoals() {
      if (this.details.conservationGoals.length > 0) return true;
      return false;
    },
    acquisitionHistoryStatement() {
      if (this.details.firstAcquisitionYear === this.details.lastAcquisitionYear) {
        return `The parcels making up this area were purchased in ${this.details.firstAcquisitionYear}.`;
      }
      return `The parcels making up this area were purchased between ${this.details.firstAcquisitionYear} and ${this.details.lastAcquisitionYear}.`;
    },
  },
  async mounted() {
    window.scrollTo(0, 0);
    if (!this.details) {
      if (this.$store.state.loaded) {
        this.initializeMap();
        this.$store.dispatch('getWildlifeAreaUnitDetails', this.wildlifeAreaUnitURLName);
      } else {
        const unwatch = this.$watch('wildlifeAreaUnit', () => {
          this.initializeMap();
          this.$store.dispatch('getWildlifeAreaUnitDetails', this.wildlifeAreaUnitURLName);
          unwatch();
        });
      }
    } else {
      this.initializeMap();
    }
  },
  methods: {
    async initializeMap() {
      this.esriModules = await loadEsriModules();
      const {
        mapView,
        unitLayer,
      } = CreateMap(this.esriModules);
      unitLayer.definitionExpression = `SUBUNIT_ID = ${this.wildlifeAreaUnit.facilityId}`;
      mapView.whenLayerView(unitLayer)
        .then((layerView) => {
          this.esriModules.WatchUtils.once(
            layerView,
            'updating',
            () => {
              layerView.queryExtent().then((result) => {
                mapView.goTo(result.extent.clone().expand(2), {
                  duration: 600,
                  easing: 'ease-in-out',
                });
              });
            },
          );
        });
    },
  },
};
</script>

