import { formatStringForURL } from '@/helpers/urlSanitization';
import { parseMarkdownLinks } from '@/helpers/parseMarkdownLinks';

export async function fetchWildlifeAreaUnits() {
  return window.fetch(
    `${process.env.VUE_APP_WLAU_SERVICE_URL}/query?where=1%3D1&returnGeometry=false&outFields=SUBUNIT_ID%2C+WA_NAME%2C+WLA_NAME&f=json`,
  )
    .then(response => response.json())
    .then(results => results.features)
    .then(features => features.map(feature => ({
      facilityId: feature.attributes.SUBUNIT_ID,
      unitName: feature.attributes.WA_Name,
      wildlifeAreaName: feature.attributes.WLA_Name,
      urlSafeName: formatStringForURL(feature.attributes.WA_Name),
    })));
}

