export default function createMap(esriModules) {
  const vectorBasemap = new esriModules.Basemap({
    baseLayers: [
      new esriModules.VectorTileLayer({
        url: process.env.VUE_APP_VECTOR_BASEMAP_URL,
      }),
    ],
  });
  const map = new esriModules.Map({
    basemap: 'topo',
  });
  const mapView = new esriModules.MapView({
    container: 'map',
    map,
    center: [-120.4472, 47.3826],
    zoom: 5.5,
  });
  const unitLayer = new esriModules.FeatureLayer({
    url: process.env.VUE_APP_WLAU_SERVICE_URL,
    outFields: [
      'SUBUNIT_ID',
    ],
  });
  unitLayer.renderer = {
    type: 'simple',
    symbol: {
      type: 'simple-fill',
      color: [190, 110, 70, 0.7],
      outline: {
        color: [0, 0, 0, 0],
      },
    },
  };
  map.add(unitLayer);
  return {
    map,
    mapView,
    unitLayer,
  };
}
