<template>
  <section class="accordion">
    <header
      class="accordion-header"
      @click="toggleAccordion"
      :class="{ showContent, active: showContent }"
      @mouseover="$emit('hoverOverHeader')"
      @mouseout="$emit('hoverOutHeader')"
    >
      {{ label }}
    </header>
    <article
      v-if="showContent"
      class="accordion-custom-content"
    >
      <slot
        name="accordion-custom-content"
      ></slot>
    </article>
  </section>
</template>

<script>
export default {
  name: 'accordion',
  props: [
    'label',
  ],
  data() {
    return {
      showContent: false,
    };
  },
  methods: {
    toggleAccordion() {
      this.showContent = !this.showContent;
    },
  },
};
</script>

<style lang="scss">
@import '@/assets/app_specific.scss';

$indent: 50px;


.accordion {
  .accordion-header {
    padding: 3px 12px !important;
    margin: 8px 0px;
  }

  .accordion-custom-content .accordion .accordion-header {
    margin-right: $indent;
    background-color: $orange;
    border-color: $orange;
    padding: 3px 12px !important;
    font-size: 18px;

    &.active {
      color: $white;
    }

    &:hover {
      background-color: $orange !important;
      opacity: 0.8;
    }
  }

  .accordion-custom-content > *:not(.accordion):not(ul) {
    margin-right: $indent;
    margin-left: 15px;
  }

  .accordion-custom-content > ul {
    margin-right: $indent;
  }

  .accordion-custom-content > table {
    width: auto;
  }
}
</style>
