import Vue from 'vue';
import Router from 'vue-router';
import BrowseWildlifeAreas from './views/BrowseWildlifeAreas.vue';
import WildlifeAreaUnit from './views/WildlifeAreaUnit.vue';
import NotFound from './views/NotFound.vue';

Vue.use(Router);

export default new Router({
  base: process.env.VUE_APP_ROUTER_BASE,
  mode: process.env.VUE_APP_ROUTER_MODE,
  routes: [
    {
      path: '/',
      name: 'browseWildlifeAreas',
      component: BrowseWildlifeAreas,
    },
    {
      path: '/:wildlifeAreaUnitName',
      name: 'wildlifeAreaUnit',
      component: WildlifeAreaUnit,
    },
    {
      path: '/*',
      name: '404',
      component: NotFound,
    },
  ],
});
